.interna_produtos {
  background: url("../img/bg-produtos.jpg") no-repeat center;
  background-size: cover;
}

.produto_item {
  padding-top: 60px;
}

.produto_item h2 {
  color: $branco;
  @include tipo-1(36);
  text-transform: uppercase;
  letter-spacing: 0.1em;
  font-weight: bold;
  position: relative;
  top: -180px;
  text-align: center;
  margin-bottom: -56px;
}

.produto_item h2:after,
.produto_item h2:before {
  content: "";
  display: block;
  width: 60px;
  height: 3px;
  background: $branco;
  margin: 6px auto 8px auto;
}

.produto_icone {
  background: $preto;
  padding: 70px 0;
}

.produto_icone img {
  margin: 0 auto;
}

.produto_info {
  background: $preto;
}

.produto_info p {
  color: $branco;
  @include tipo-1(18);
  height: 180px;
  padding: 30px 40px;
}

.produto_info ul li {
  background: $amarelo;
  text-transform: uppercase;
  @include tipo-1(18);
  font-weight: bold;
  float: left;
  width: 229px;
  height: 49px;
  text-align: center;
  padding-top: 16px;
}

.produto_info ul li:nth-child(1),
.produto_info ul li:nth-child(2) {
  margin-bottom: 2px;
}

.produto_info ul li:nth-child(even) {
  margin-left: 2px;
}

/*Orçamento*/

.orcamento {
  background: $preto;
  width: 100%;
  margin-top: 60px;
  padding: 40px 0;
}

.orcamento h2 {
  color: $amarelo;
}

.orcamento h2:after {
  background: $amarelo;
}

.form {
  padding-right: 60px;
}

.form label {
  display: block;
  color: $branco;
  @include tipo-2(18);
  margin-bottom: 4px;
}

.form input {
  display: block;
  width: 100%;
  border: 4px solid $amarelo;
  background: none;
  color: $branco;
  padding: 7px 10px;
  margin-bottom: 10px;
  outline: none;
  @include tipo-2(14);
}

.form textarea {
  display: block;
  width: 100%;
  height: 120px;
  border: 4px solid $amarelo;
  background: none;
  color: $branco;
  padding: 7px 10px;
  margin-bottom: 20px;
  outline: none;
  @include tipo-2(14);
}

.form button {
  padding: 7px 40px;
  background: none;
}

.orcamento_dados {
  color: $branco;
}

.orcamento_dados h3 {
  @include tipo-1(18);
  text-transform: uppercase;
  color: $amarelo;
  font-weight: bold;
}

.orcamento_dados span {
  display: block;
  @include tipo-2(18);
}

.orcamento_dados span:nth-of-type(2) {
  margin-bottom: 40px;
}

.orcamento_dados ul {
  padding-right: 60px;
}

.orcamento_dados ul li {
  @include tipo-2(14);
  padding: 10px 15px;
}

.orcamento_dados ul li:nth-child(odd) {
  background: $cinza;
}

.orcamento_dados p {
  margin: 8px 0;
}
